// src/pages/Foods.js
import React from 'react';

const foods = [
  { id: 1, name: 'Pizza', description: 'Lezzetli pizza', price: 10 },
  { id: 2, name: 'Pasta', description: 'Taze makarna', price: 15 },
  // Diğer yemekler...
];

const Foods = ({ addToCart }) => {
  return (
    <div>
      <h1>Yemekler</h1>
      <ul>
        {foods.map((food) => (
          <li key={food.id}>
            <h2>{food.name}</h2>
            <p>{food.description}</p>
            <p>Fiyat: {food.price} TL</p>
            <button>Detay</button> {/* Detay butonu */}
            <button onClick={() => addToCart(food)}>Sepete Ekle</button> {/* Sepete ekle butonu */}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Foods;
